import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import { Modal, ModalProps } from "./ModalMain";
import { Button } from "../button";
import { ModalMessage } from "./ModalMessage";
import { Combine } from "../_type";
import { useTranslation } from "../i18n";

/**
 * `Modal.confirm` 
 */
export interface ConfirmOptions {
  /**
   * 
   */
  message: string;
  /**
   * 
   */
  description?: string;

  /**
   * 
   */
  okText?: React.ReactNode;

  /**
   * 
   */
  cancelText?: React.ReactNode;

  /**
   * 
   *
   * 
   * @default false
   */
  disableCloseIcon?: boolean;

  /**
   *  ModalProps  `size` 
   */
  size?: ModalProps["size"];
}

/**
 * API 
 * @param options
 * @returns  true  false 
 */
export function confirm(options: ConfirmOptions) {
  return new Promise<boolean>(resolve => {
    const el = document.createElement("div");
    ReactDOM.render(
      <ModalConfirmAPI
        {...options}
        onOk={() => resolve(true)}
        onCancel={() => resolve(false)}
        onExited={() => {
          ReactDOM.unmountComponentAtNode(el);
        }}
      />,
      el
    );
  });
}

function ModalConfirmAPI({
  onOk,
  onCancel,
  ...confirmModalProps
}: ModalConfirmProps) {
  const [visible, setVisible] = useState(false);

  // 
  useEffect(() => setVisible(true), []);

  const ok = () => {
    setVisible(false);
    onOk();
  };

  const cancel = () => {
    setVisible(false);
    onCancel();
  };

  return (
    <ModalConfirm
      visible={visible}
      onOk={ok}
      onCancel={cancel}
      onClose={cancel}
      {...confirmModalProps}
    />
  );
}

export interface ModalConfirmProps extends Combine<ModalProps, ConfirmOptions> {
  onOk(): void;
  onCancel(): void;
}

/**
 *  ModalConfirm 
 *  `Modal.confirm()` API 
 */
export function ModalConfirm({
  message,
  description,
  okText,
  cancelText,
  onOk,
  onCancel,
  ...modalProps
}: ModalConfirmProps) {
  const t = useTranslation();

  return (
    <Modal disableEscape {...modalProps}>
      <Modal.Body>
        <ModalMessage message={message} description={description} />
      </Modal.Body>
      <Modal.Footer>
        <Button type="primary" onClick={onOk}>
          {okText || t.okText}
        </Button>
        <Button onClick={onCancel}>{cancelText || t.cancelText}</Button>
      </Modal.Footer>
    </Modal>
  );
}
