import { createContext } from "react";

export interface CollapseContextValue {
  /**
   *  ID
   */
  activeIds: string[];

  /**
   * 
   */
  onActive: (
    activeIds: string[],
    context: { event: React.SyntheticEvent }
  ) => void;
}

export const CollapseContext = createContext<CollapseContextValue>({
  activeIds: [],
  onActive: () => null,
});
